@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300;400;600;700;900&display=swap");

:root {
  /* Tamaños */
  --nav-height: 8vh;

  /* Colores */
  --white: #fff;
  --nav-background: #f9f9f9;
  --primary-background: #fcfcfc;
  --card-white: #fafafa;
  --primary-color: #c171dd;
  --background-black: #323232;
  --secondary-black: #505050;
  --border-colors: #c2c2c2;
  --primary-font: #32323200;
}

::-webkit-scrollbar {
  /* width: 10px; */
  background-color: rgba(255, 255, 255, 0);
}

::-webkit-scrollbar-thumb {
  width: 10px;
  background-color: rgba(180, 169, 154, 0.356);
  border: 4px solid transparent;
  border-radius: 100px;
  background-clip: content-box;
  margin-top: 40px;
}

::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0);
}

* {
  font-family: "Titillium Web";
  color: var(--background-black);
}

body {
  max-width: 100vw;
  background-color: var(--primary-background);
  /* overflow-x: hidden; */
}

#root {
  max-width: 100vw;
}

html {
  scroll-behavior: smooth;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}
