@media screen and (max-width: 1400px) {
  .nav {
    gap: 10%;
  }

  .home {
    margin-left: 0vw;
  }

  .home-waves {
    display: none;
  }

  .value-cont-item {
    height: 40vh;
    min-width: 300px;
  }

  .contact-cont-form-input {
    width: 20vw;
  }
  .contact-cont-form-textarea {
    width: 20vw;
  }
}

@media screen and (max-width: 1100px) {
  .value {
    height: auto;
  }
  .value-cont {
    flex-direction: column;
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .about-cont-text {
    width: 45%;
    font-size: 20px;
  }
  .about-cont-img {
    height: 50%;
  }

  .contact-cont {
    justify-content: space-evenly;
  }
  .contact-cont-form {
    width: 40%;
  }
  .contact-cont-form-input {
    width: 65%;
  }
  .contact-cont-form-textarea {
    width: 65%;
  }

  .contact-cont-media {
    width: 50%;
  }
}

@media screen and (max-width: 918px) {
  .contact {
    height: auto;
  }
  .contact-cont {
    flex-direction: column;
    padding-top: 40px;
    padding-bottom: 40px;
    gap: 40px;
  }
  .contact-cont-media-social {
    border-right: none;
  }
  .contact-cont-media-social-link {
    width: 100%;
  }
  .contact-cont-media-middle {
    flex-direction: column;
  }
  .contact-cont-media-calendar {
    flex-direction: row;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    align-items: center;
    justify-content: flex-start;
    border-top: var(--border-colors) 1px solid;
  }
  .contact-cont-media-social-link:last-child {
    /* border-bottom: none; */

    margin-bottom: 0vh;
  }
}

@media screen and (max-width: 780px) {
  .nav-list {
    display: none;
  }
  .value-text {
    width: 90%;
    text-align: center;
  }
  .about {
    height: auto;
  }
  .about-cont {
    flex-direction: column;
  }
  .about-cont-text {
    width: 80%;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .about-cont-img {
    height: 50vh;
    margin-bottom: 50px;
  }

  .contact-text {
    width: 80%;
    align-self: center;
  }

  .contact-cont-form-input {
    width: 100%;
  }
  .contact-cont-form-textarea {
    width: 100%;
  }
}

@media screen and (max-width: 530px) {
  .value {
    max-width: 100%;
  }

  .value-waves {
    width: 100vw;
  }
}

@media screen and (max-width: 450px) {
  .home-title {
    font-size: 85px;
  }
  .home-text {
    font-size: 30px;
  }

  .about-cont-img {
    height: 35vh;
  }
  .contact-cont-media {
    width: 80%;
  }
  .contact-cont-form {
    width: 60%;
  }
}
