/* Aqui hay clases generales */

.hilight {
  color: var(
    --primary-color
  ) !important; /* El important se usa por si en alguna clase se establece el color de letra tambien */
}

/* Aqui empieza el apartado del navbar */
.nav {
  z-index: 1000;
  width: 100%;
  height: var(--nav-height);
  background-color: var(--nav-background);
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-evenly;
  gap: 25vw;
  align-items: center;
}

.nav-logo {
  user-select: none;
  font-family: "Titillium Web";
  font-weight: 700;
  font-size: 50px;
  text-align: center;
  color: var(--background-black);
  margin-bottom: 5px;
}

.nav-logo-special {
  color: var(--primary-color);
  font-size: 30px;
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  gap: 40px;
}

.nav-list-item-link {
  font-family: "Titillium Web";
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  text-decoration: none;
  color: var(--background-black);
  transition: 0.2s;
}

.nav-list-item-link:hover {
  transition: 0.2s;
  color: var(--primary-color);
}

.nav-button {
  border: none;
  border-radius: 8px;
  padding: 15px;
  cursor: pointer;
  transition: 0.4s;
  background-color: var(--primary-color);
  user-select: none;

  font-family: "Titillium Web";
  color: var(--white);
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
}

.nav-button:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  scale: 1.02;
}

/* Aqui empìeza la parte de home */

.home {
  height: calc(100vh - var(--nav-height));
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: -20vw;
}

.home-waves {
  position: absolute;
  z-index: -1;
  right: 0;
  top: 0;
}

.home-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 12vh;
  line-height: 152px;
  /* text-align: center; */
}

.home-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 500;
  font-size: 6vh;
  /* line-height: 80px; */
  text-align: center;
  color: var(--secondary-black);
}

.home-button {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 1.8vh;
  text-align: center;
  color: var(--white);
  background-color: var(--primary-color);
  border: none;
  border-radius: 8px;
  padding: 18px 18px 18px 18px;
  cursor: pointer;
  transition: 0.4s;
  margin-top: 10vh;
  user-select: none;
  text-decoration: none;
}

.home-button:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  scale: 1.02;
}

.value {
  height: calc(100vh);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.value-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 8vh;
  text-align: center;
  margin-top: 5vh;
}

.value-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
}

.value-waves {
  position: absolute;
  z-index: -1;
  left: 0;
}

.value-cont {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 6vw;
}

.value-cont-item {
  display: flex;
  height: 36vh;
  width: 20vw;
  flex-direction: column;
  align-items: center;
  background-color: var(--card-white);
  box-shadow: 0px 0px 52px -13px rgba(0, 0, 0, 0.25);
  border-radius: 33px;
  gap: 20px;
  transition: 0.4s;
}

.value-cont-item:hover {
  scale: 1.02;
}
.value-cont:hover .value-cont-item:not(:hover) {
  filter: blur(2px);
}

.value-cont-item-img {
  width: 100%;
  height: 40%;
  object-fit: cover;
  border-radius: 33px 33px 0px 0px;
}

.value-cont-item-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 37px;
}

.value-cont-item-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  margin-right: 30px;
  margin-left: 30px;
}

/* Aquí empieza la seccion de sobre nosotros */

.about {
  height: calc(100vh);
  width: 100%;
  display: flex;
  flex-direction: column;
}

.about-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 8vh;
  text-align: center;
  margin-top: 4vh;
}

.about-cont {
  height: 100%;
  display: flex;
  /* background-color: #c171dd; */
  align-items: center;
  justify-content: space-evenly;
}

.about-cont-text {
  width: 33%;
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
}

.about-cont-img {
  height: 70%;
}

/* Aquí empieza el apartado  de contacto */

.contact {
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.contact-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 8vh;
  text-align: center;
  margin-top: 4vh;
}

.contact-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 600;
  text-align: center;
  font-size: 24px;
  line-height: 37px;
}

.contact-cont {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.contact-cont-form {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.contact-cont-form-input {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;

  height: 6vh;
  width: 15vw;
  border: none;
  padding-left: 20px;
  background: #f2f2f2;
  border-radius: 13px;
  outline: invert;
}

.contact-cont-form-textarea {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;

  height: 20vh;
  width: 15vw;
  border: none;
  padding-left: 20px;
  padding-top: 16px;
  background: #f2f2f2;
  border-radius: 13px;
  outline: invert;
  resize: none;
}

.contact-cont-form-button {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 1.8vh;
  text-align: center;

  color: var(--white);
  background-color: var(--primary-color);
  border: none;
  border-radius: 8px;
  padding: 14px 48px 14px 48px;
  /* cursor: pointer; */
  transition: 0.4s;
  user-select: none;
  cursor: not-allowed;
}

.contact-cont-form-button:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  scale: 1.02;
}

.contact-cont-media {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background-color: #505050; */
}

.contact-cont-media-middle {
  width: 80%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: var(--border-colors) 1px solid;
}

.contact-cont-media-social {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 20px;
  border-right: var(--border-colors) 1px solid;
}

.contact-cont-media-social-link {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  width: 90%;
  padding-bottom: 20px;
  border-bottom: var(--border-colors) 1px solid;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
}

.contact-cont-media-social-link:last-child {
  border-bottom: none;
  margin-bottom: -2vh;
}

.contact-cont-media-calendar {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.contact-cont-media-calendar-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  user-select: none;
}

.contact-cont-media-policies {
  padding-top: 20px;
}

.contact-cont-media-policies-text {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  user-select: none;
}

.contact-wave {
  position: absolute;
  width: 100%;
  top: 370%;
}
